import React, { useState } from "react";
import { Grid, Paper, Typography, Button, TextField, Chip, Divider, CircularProgress, Box, Tabs, Tab, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Accordion, AccordionSummary, AccordionDetails } from "@mui/material";
import axios from "axios";
import RawOutputVisualizer from "./RawOutputVisualizer";
import AgentSummary from "./AgentSummary";
import AgentCharts from "./AgentCharts";
import AgentAlerts from "./AgentAlerts";

const DEFAULT_TICKERS = "AAPL,MSFT";
const DEFAULT_START_DATE = "2024-01-01";
const DEFAULT_END_DATE = "2024-04-30";
const DEFAULT_CASH = 100000;

const SIGNAL_COLORS = {
  bullish: 'success',
  bearish: 'error',
  neutral: 'default',
};

export default function HedgeFundDashboard() {
  const [tickers, setTickers] = useState(DEFAULT_TICKERS);
  const [startDate, setStartDate] = useState(DEFAULT_START_DATE);
  const [endDate, setEndDate] = useState(DEFAULT_END_DATE);
  const [initialCash, setInitialCash] = useState(DEFAULT_CASH);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState(null);
  const [tab, setTab] = useState(0);

  const handleRun = async () => {
    setLoading(true);
    setResult(null);
    setError(null);
    try {
      const response = await axios.post("/api/run", {
        tickers,
        start_date: startDate,
        end_date: endDate,
        initial_cash: initialCash,
      });
      setResult(response.data);
    } catch (err) {
      setError(err.response?.data?.error || err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Paper elevation={2} sx={{ p: 4 }}>
      <Typography variant="h5" fontWeight={600} gutterBottom>
        Run a Multi-Agent Hedge Fund Simulation
      </Typography>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12} sm={6} md={3}>
          <TextField label="Tickers" value={tickers} onChange={e => setTickers(e.target.value)} fullWidth helperText="Comma-separated (e.g., AAPL,MSFT)" />
        </Grid>
        <Grid item xs={12} sm={6} md={2}>
          <TextField label="Start Date" type="date" value={startDate} onChange={e => setStartDate(e.target.value)} fullWidth InputLabelProps={{ shrink: true }} />
        </Grid>
        <Grid item xs={12} sm={6} md={2}>
          <TextField label="End Date" type="date" value={endDate} onChange={e => setEndDate(e.target.value)} fullWidth InputLabelProps={{ shrink: true }} />
        </Grid>
        <Grid item xs={12} sm={6} md={2}>
          <TextField label="Initial Cash ($)" type="number" value={initialCash} onChange={e => setInitialCash(Number(e.target.value))} fullWidth />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Button variant="contained" color="primary" onClick={handleRun} fullWidth disabled={loading} size="large">
            {loading ? <CircularProgress size={24} /> : "Run Simulation"}
          </Button>
        </Grid>
      </Grid>
      <Divider sx={{ my: 3 }} />
      {error && <RawOutputVisualizer raw={JSON.stringify(error, null, 2)} />}
      {result && (
        <Box>
          <Tabs value={tab} onChange={(_, v) => setTab(v)} sx={{ mb: 2 }}>
            <Tab label="Summary" />
            <Tab label="Agents" />
            <Tab label="Raw Output" />
          </Tabs>
          {tab === 0 && (
            <Box>
              {result.agents && <AgentAlerts agents={result.agents} />}
              {result.agents && <AgentCharts agents={result.agents} />}
              {result.agents && (
                <Box>
                  <Typography variant="h6" fontWeight={600} sx={{ mt: 2 }}>
                    Agent Analysis
                  </Typography>
                  <Grid container spacing={2} sx={{ mt: 1 }}>
                    {Object.entries(result.agents).map(([agent, data]) => (
                      <Grid item xs={12} sm={6} md={4} key={agent}>
                        <Paper elevation={2} sx={{ p: 2, minHeight: 140 }}>
                          <Typography variant="subtitle1" color="primary" fontWeight={700}>{agent}</Typography>
                          {typeof data === 'object' ? (
                            Object.entries(data).map(([ticker, info]) => (
                              <Box key={ticker} sx={{ mt: 1, mb: 1 }}>
                                <Typography fontWeight={600}>{ticker}</Typography>
                                {info.signal && <Chip label={info.signal.toUpperCase()} color={SIGNAL_COLORS[info.signal?.toLowerCase()] || 'default'} sx={{ ml: 1, mb: 1 }} />}
                                {info.confidence !== undefined && <Typography variant="body2">Confidence: {info.confidence}%</Typography>}
                                {info.reasoning && (() => {
                                  let parsed = info.reasoning;
                                  if (typeof info.reasoning === "string" && info.reasoning.trim().startsWith("{")) {
                                    try {
                                      parsed = JSON.parse(info.reasoning);
                                    } catch {
                                      return <RawOutputVisualizer raw={info.reasoning} />;
                                    }
                                  }
                                  if (parsed && typeof parsed === "object" && !Array.isArray(parsed)) {
                                    return (
                                      <Box sx={{ mt: 1 }}>
                                        <Grid container spacing={1}>
                                          {Object.entries(parsed).map(([k, v]) => (
                                            <Grid item xs={12} sm={6} key={k}>
                                              <Paper elevation={1} sx={{ p: 1.5 }}>
                                                <Typography fontWeight={700} sx={{ mb: 0.5 }}>{k.replace(/_/g, ' ')}</Typography>
                                                {v.signal && <Chip label={v.signal.toUpperCase()} color={SIGNAL_COLORS[v.signal?.toLowerCase()] || 'default'} size="small" sx={{ mb: 0.5 }} />}
                                                {v.details && <Typography variant="body2">{v.details}</Typography>}
                                                {Object.entries(v).map(([vk, vv]) => (
                                                  ["signal", "details"].includes(vk) ? null :
                                                    <Typography key={vk} variant="caption" sx={{ display: 'block', color: 'text.secondary' }}>{vk.replace(/_/g, ' ')}: {String(vv)}</Typography>
                                                ))}
                                              </Paper>
                                            </Grid>
                                          ))}
                                        </Grid>
                                      </Box>
                                    );
                                  }
                                  return <RawOutputVisualizer raw={typeof parsed === 'string' ? parsed : JSON.stringify(parsed, null, 2)} />;
                                })()}
                                {Object.entries(info).map(([key, val]) => (
                                  ["signal", "confidence", "reasoning"].includes(key) ? null :
                                    (typeof val === 'object' || Array.isArray(val)) ? (
                                      <Accordion key={key+"-extra"}>
                                        <AccordionSummary>
                                          <Typography>{key.replace(/_/g, ' ')}</Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                          <RawOutputVisualizer raw={JSON.stringify(val, null, 2)} />
                                        </AccordionDetails>
                                      </Accordion>
                                    ) : (
                                      <Typography key={key+"-extra"} variant="body2" sx={{ mt: 0.5 }}>
                                        {key.replace(/_/g, ' ')}: {String(val)}
                                      </Typography>
                                    )
                                ))}
                              </Box>
                            ))
                          ) : (
                            <RawOutputVisualizer raw={JSON.stringify(data, null, 2)} />
                          )}
                        </Paper>
                      </Grid>
                    ))}
                  </Grid>
                </Box>
              )}
              {result.decisions && (
                <Box>
                  <Typography variant="h6" fontWeight={600} sx={{ mt: 2 }}>
                    Trading Decisions
                  </Typography>
                  <TableContainer component={Paper} sx={{ mt: 2 }}>
                    <Table>
                      <TableHead>
                        <TableRow>
                          <TableCell>Ticker</TableCell>
                          <TableCell>Action</TableCell>
                          <TableCell>Quantity</TableCell>
                          <TableCell>Confidence</TableCell>
                          <TableCell>Reasoning</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {Object.entries(result.decisions).map(([ticker, d]) => (
                          <TableRow key={ticker}>
                            <TableCell>{ticker}</TableCell>
                            <TableCell><Chip label={d.action} color={SIGNAL_COLORS[d.action?.toLowerCase()] || 'default'} /></TableCell>
                            <TableCell>{d.quantity}</TableCell>
                            <TableCell>{d.confidence}%</TableCell>
                            <TableCell sx={{ maxWidth: 300, whiteSpace: 'pre-line' }}>{d.reasoning}</TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                </Box>
              )}
            </Box>
          )}
          {tab === 1 && result.agents && (
            <Box>
              <Typography variant="h6" fontWeight={600} sx={{ mt: 2 }}>
                Agent Analysis
              </Typography>
              <Grid container spacing={2} sx={{ mt: 1 }}>
                {Object.entries(result.agents).map(([agent, data]) => (
                  <Grid item xs={12} sm={6} md={4} key={agent}>
                    <Paper elevation={2} sx={{ p: 2, minHeight: 140 }}>
                      <Typography variant="subtitle1" color="primary" fontWeight={700}>{agent}</Typography>
                      {typeof data === 'object' ? (
                        Object.entries(data).map(([ticker, info]) => (
                          <Box key={ticker} sx={{ mt: 1, mb: 1 }}>
                            <Typography fontWeight={600}>{ticker}</Typography>
                            {info.signal && <Chip label={info.signal.toUpperCase()} color={SIGNAL_COLORS[info.signal?.toLowerCase()] || 'default'} sx={{ ml: 1, mb: 1 }} />}
                            {info.confidence !== undefined && <Typography variant="body2">Confidence: {info.confidence}%</Typography>}
                            {info.reasoning && (() => {
  let parsed = info.reasoning;
  if (typeof info.reasoning === "string" && info.reasoning.trim().startsWith("{")) {
    try {
      parsed = JSON.parse(info.reasoning);
    } catch {
      return <RawOutputVisualizer raw={info.reasoning} />;
    }
  }
  if (parsed && typeof parsed === "object" && !Array.isArray(parsed)) {
    return (
      <Box sx={{ mt: 1 }}>
        <Grid container spacing={1}>
          {Object.entries(parsed).map(([k, v]) => (
            <Grid item xs={12} sm={6} key={k}>
              <Paper elevation={1} sx={{ p: 1.5 }}>
                <Typography fontWeight={700} sx={{ mb: 0.5 }}>{k.replace(/_/g, ' ')}</Typography>
                {v.signal && <Chip label={v.signal.toUpperCase()} color={SIGNAL_COLORS[v.signal?.toLowerCase()] || 'default'} size="small" sx={{ mb: 0.5 }} />}
                {v.details && <Typography variant="body2">{v.details}</Typography>}
                {/* Show any extra fields in v */}
                {Object.entries(v).map(([vk, vv]) => (
                  ["signal", "details"].includes(vk) ? null :
                    <Typography key={vk} variant="caption" sx={{ display: 'block', color: 'text.secondary' }}>{vk.replace(/_/g, ' ')}: {String(vv)}</Typography>
                ))}
              </Paper>
            </Grid>
          ))}
        </Grid>
      </Box>
    );
  }
  return <RawOutputVisualizer raw={typeof parsed === 'string' ? parsed : JSON.stringify(parsed, null, 2)} />;
})()}
                          </Box>
                        ))
                      ) : (
                        <Typography variant="body2">{data}</Typography>
                      )}
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}
          {tab === 2 && (
            <Box sx={{ mt: 2 }}>
              <RawOutputVisualizer raw={result.raw || result.output || ""} />
            </Box>
          )}
        </Box>
      )}
    </Paper>
  );
}
