import React from "react";
import { Box, Typography, Grid, Paper } from "@mui/material";
import { Pie, Bar } from "react-chartjs-2";
import { Chart, ArcElement, Tooltip as ChartTooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js';

Chart.register(ArcElement, ChartTooltip, Legend, BarElement, CategoryScale, LinearScale);

function aggregateSignals(agents) {
  // Count bullish/bearish/neutral per ticker
  const tickers = {};
  Object.values(agents).forEach(agent => {
    if (typeof agent !== 'object') return;
    Object.entries(agent).forEach(([ticker, info]) => {
      if (!tickers[ticker]) tickers[ticker] = { bullish: 0, bearish: 0, neutral: 0 };
      if (info.signal) {
        const s = info.signal.toLowerCase();
        if (tickers[ticker][s] !== undefined) tickers[ticker][s]++;
      }
    });
  });
  return tickers;
}

function aggregateConfidence(agents) {
  // Average confidence per ticker
  const tickers = {};
  Object.values(agents).forEach(agent => {
    if (typeof agent !== 'object') return;
    Object.entries(agent).forEach(([ticker, info]) => {
      if (!tickers[ticker]) tickers[ticker] = { total: 0, count: 0 };
      if (typeof info.confidence === 'number') {
        tickers[ticker].total += info.confidence;
        tickers[ticker].count++;
      }
    });
  });
  Object.entries(tickers).forEach(([ticker, v]) => {
    v.avg = v.count ? v.total / v.count : 0;
  });
  return tickers;
}

export default function AgentCharts({ agents }) {
  if (!agents) return null;
  const signalAgg = aggregateSignals(agents);
  const confAgg = aggregateConfidence(agents);
  const tickers = Object.keys(signalAgg);

  return (
    <Box sx={{ mb: 3 }}>
      <Typography variant="h6" fontWeight={600} sx={{ mb: 2 }}>Agent Consensus Charts</Typography>
      <Grid container spacing={3}>
        {tickers.map(ticker => (
          <Grid item xs={12} md={6} key={ticker}>
            <Paper elevation={2} sx={{ p: 2 }}>
              <Typography fontWeight={700} sx={{ mb: 1 }}>{ticker}</Typography>
              <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                <Box sx={{ width: 180, height: 180 }}>
                  <Pie
                    data={{
                      labels: ['Bullish', 'Bearish', 'Neutral'],
                      datasets: [{
                        data: [signalAgg[ticker].bullish, signalAgg[ticker].bearish, signalAgg[ticker].neutral],
                        backgroundColor: ['#4caf50', '#f44336', '#bdbdbd'],
                      }],
                    }}
                    options={{ plugins: { legend: { position: 'bottom' } } }}
                  />
                </Box>
                <Box sx={{ flex: 1, ml: 3 }}>
                  <Bar
                    data={{
                      labels: ['Avg. Confidence'],
                      datasets: [
                        {
                          label: ticker,
                          data: [confAgg[ticker]?.avg || 0],
                          backgroundColor: '#1976d2',
                        },
                      ],
                    }}
                    options={{
                      indexAxis: 'y',
                      scales: { x: { min: 0, max: 100 } },
                      plugins: { legend: { display: false } },
                    }}
                    height={60}
                  />
                </Box>
              </Box>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
